.wrapper-fiolet {
    background: url('../img/bgf.jpg');
    background-repeat: no-repeat;
    background-size: cover;


    .logo-letter {
        color: #a18bed
    }

    .checkbox-tools:checked + label {
        box-shadow: 0px 0px 15px 4px #FFEFFF;

        .label-title {
            background: #EBD8FF;

        }

        .label-summ {
            color: #A18BED;

        }
    }
}


.btn-fiolet {
    background: linear-gradient(283.73deg, #A58CF0 26.23%, #EBD8FF 97.27%);

    &:hover {
        background: linear-gradient(120.73deg, #A58CF0 26.23%, #EBD8FF 97.27%);
    }
}
